import {tkHandle} from '../service/getData';
export default {
  data() {
    return {
      hasFooter: false,
      catalog: []
    };
  },
  components: {},
  async mounted() {
    let response = await tkHandle(this.$route.params.questionPlanID);
    let len = response.data.data.length - 3;
    this.catalog = response.data.data.slice(0, len);
  }
};



